#MultipleSlides { height: 100% }
html,
body {
  position: relative;
  height: 100%;
}

body {
  background: #eee;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}

.swiper {
  width: 832px;
  height: 468px;
  margin-top: 20px;
  @media (max-width: 1160px)  {
    width: 90%;
  }
  @media (max-width: 895px)  {
    width: 608px;
    height: 342px;
  }
  @media (max-width: 760px)  {
    width: 400px;
    height: 225px;
  }
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;

}

.swiper-zoom-container {
    width: 832px;
    height: 468px;
    @media (max-width: 895px)  {
      width: 608px;
      height: 342px;
    }
}