[data-theme="dark"]{--background: #2E2A2A;--background-header: #423E3E;--text: white}[data-theme="light"]{--background: #EFEBE9;--background-header: #BCAAA4;--text: black}.text{color:var(--text)}.app{background-color:var(--background);display:flex;flex-direction:column;color:white}.app .app__main{min-height:100vh}.app .container{margin:auto;max-width:1160px;padding:0 20px}@media (max-width: 1160px){.app .container{width:80%;margin:auto;padding:0 20px}}
